"use strict";
/*global require*/
/*global window*/
/*global Vue*/


/**
 * First we will load all of this project's JavaScript dependencies which
 * includes Vue and other libraries. It is a great starting point when
 * building robust, powerful web applications using Vue and Laravel.
 */

require('./bootstrap');
window.Vue = require('vue');
import VueRouter from 'vue-router';
Vue.use(VueRouter);

const signin=require('./components/SignInComponent');
const verification=require('./components/VerificationComponent');

Vue.component('signin-component', require('./components/SignInComponent.vue'));
// Vue.component('verification-component', require('./components/VerificationComponent'));

const routes=[
    {
        path:'/',
        component:signin
    },
    {
        path:'/verification',
        component:verification
    }
];


/**
 * Next, we will create a fresh Vue application instance and attach it to
 * the page. Then, you may begin adding components to this application
 * or customize the JavaScript scaffolding to fit your unique needs.
 */



const router=new VueRouter({
    // mode:'history',
    // hashbang: false,
    // root: '/EnergyDepot_II/public/',
    routes,
});

const app = new Vue({
    el: '#app',
    router,

});



